.user_info_container {
    max-width: 600px;
    margin: 0 auto;
    font-family: Arial, sans-serif;
    padding: 20px;
}

.user_info_form {
    display: flex;
    flex-direction: column;
}

h2 {
    text-align: center;
    margin-bottom: 20px;
}

.form_group {
    margin-bottom: 15px;
}

.form_group label {
    display: block;
    font-size: 14px;
    margin-bottom: 5px;
}

.form_group input {
    width: 100%;
    padding: 10px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.form_group input[type="radio"] {
    width: auto;
}

.btn_update_info,
.btn_delete_account {
    padding: 10px;
    font-size: 16px;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 20px;
}

.btn_update_info:hover,
.btn_delete_account:hover {
    background-color: #0056b3;
}

.btn_delete_account {
    background-color: #dc3545;
}

.btn_delete_account:hover {
    background-color: #c82333;
}
